let count = 0;


function Element (tagName, props, children) {
  	if(!(this instanceof Element)){
  		return new Element(tagName, props, children)
  	}
	this.tagName = tagName
	this.props = props
	this.children = children
}


Element.prototype.render = function () {
  var el = document.createElement(this.tagName) // 根据tagName构建
  var props = this.props
  if(this.children.length < 2){
 	props.class += ' test';
 	props['data-index'] = ++count;
  }
  for (var propName in props) { // 设置节点的DOM属性
    var propValue = props[propName]
    el.setAttribute(propName, propValue)
  }

  var children = this.children || []

  children.forEach(function (child) {
    var childEl = (child instanceof Element)
      ? child.render() // 如果子节点也是虚拟DOM，递归构建DOM节点
      : document.createTextNode(child) // 如果字符串，只构建文本节点
    el.appendChild(childEl)
  })

  return el
}

var ul = Element('div', {id: 'widgetModel'}, [
  Element('div', {'sw-role': 'cell','class':'flex-size-1 flex-middle',}, ['模块一']),
  Element('div', {'sw-role': 'cell','class':'flex-size-1 flex-middle'}, [
  	Element('div', {'sw-role': 'cell','class':'flex-size-1 flex-middle'},['模块二']),
  	Element('div', {'sw-role': 'cell','class':'flex-size-1 flex-middle'},['模块三'])]),
  Element('div', {'sw-role': 'cell','class':'flex-size-1 flex-middle'}, ['模块四'])
])




const ctrl = {
	init(){
		const html = ul.render();
		$('#widget-content').html(html);
		this.event()
	},
	event(){
		$('#widget-content').on('click','.test',(e)=>{
			e.currentTarget.innerHTML = '2222222'
		})
	}
}



module.exports = ctrl


